import { useState } from 'react';
import { Link } from 'react-router-dom';

const Register = ({ handleRegister }) => {
    const [data, setData] = useState({
        email: '',
        password: '',
    });


    const handleChange = (event) => {
        const {name, value} = event.target;
        setData({...data, [name]: value});
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        handleRegister(data)
    };

    return (
        <form className="register" onSubmit={handleSubmit}>
            <h2 className="register__title">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h2>
            <input className="register__input" name="email" type="email" placeholder="Email" minLength="6" maxLength="20" required value={data.email} onChange={handleChange}  />
            <input className="register__input" name="password" type="password" placeholder="–ü–∞—Ä–æ–ª—å" minLength="6" maxLength="20" required value={data.password} onChange={handleChange}  />
            <button className="register__submit" type="submit">–ó–∞—Ä–µ–≥–µ—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
            <p className="register__link">–£–∂–µ –∑–∞—Ä–µ–≥–µ—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã? <Link className="register__link register__link_hover" to="/sign-in">–í–æ–π—Ç–∏</Link></p>
        </form>
    );
}

export default Register;