import checkMark from '../images/check_mark.svg';
import cross from '../images/cross.svg';

const InfoTooltip = ({ isOpen, onClose, isInfoTooltipStatus }) => {
    return (
        <div className={`popup ${isOpen ? 'popup_is-open' : ' '}`} >
            <div className='popup__check'>
                <img className='popup__check-cross' src={isInfoTooltipStatus ? checkMark : cross} alt='–°—Ç–∞—Ç—É—Å –ø—Ä–æ–≤–µ–¥–µ–Ω–Ω–æ–π –æ–ø–µ—Ä–∞—Ü–∏–∏' />
                <h2 className='popup__check-about'>{isInfoTooltipStatus ? '–í—ã —É—Å–ø–µ—à–Ω–æ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–ª–∏—Å—å!' : '–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â–µ —Ä–∞–∑.'}</h2>
                <button className='popup__close' type='reset' onClick={onClose}></button>
            </div>
        </div>
    );
}

export default InfoTooltip;