class Api {
    constructor({baseUrl, headers}) {
    this._baseUrl = baseUrl;
    this._headers = headers;
    }

    _addCommonFetchForAllApis(url, init) {
        return fetch(url, init)
        .then((response) => {
            if (response.ok) {
                return response.json()
                }
            return Promise.reject(`–í–æ–∑–Ω–∏–∫–ª–∞ –æ—à–∏–±–∫–∞: ${response.status}`)
        })
    }
    
    //–ü–æ–ª—É—á–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫–∏ —Å —Å–µ—Ä–≤–µ—Ä–∞
    getInitialCards(jwt) {
        return this._addCommonFetchForAllApis(`${this._baseUrl}/cards`, {
            method: "GET",
            headers: this._headers,
            credentials: 'include'
        })
    }

    //–ë–ª–æ–∫ —Ä–∞–±–æ—Ç—ã —Å –∫–∞—Ä—Ç–æ—á–∫–∞–º–∏
    //–î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏
    addNewCard(card) {
        return this._addCommonFetchForAllApis(`${this._baseUrl}/cards`, {
            method: 'POST',
            headers: this._headers,
            body: JSON.stringify({
                name: card.name,
                link: card.link,
                }),
            credentials: 'include'
            });
    }

    //–£–¥–∞–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏
    deleteCard(card) {
        return this._addCommonFetchForAllApis(`${this._baseUrl}/cards/${card._id}`, {
            method: 'DELETE',
            headers: this._headers,
            body: JSON.stringify(),
            credentials: 'include'
        });
    }

    //–õ–∞–π–∫ –∫–∞—Ä—Ç–∏–Ω–∫–µ
    addLikeCard(card) {
        return this._addCommonFetchForAllApis(`${this._baseUrl}/cards/likes/${card._id}`, {
            method: 'PUT',
            headers: this._headers,
            body: JSON.stringify(),
            credentials: 'include'
        });
    }

    //–£–¥–∞–ª–µ–Ω–∏–µ –ª–∞–π–∫ –∫–∞—Ä—Ç–∏–Ω–∫–µ
    deleteLikeCard(card) {
        return this._addCommonFetchForAllApis(`${this._baseUrl}/cards/likes/${card._id}`, {
            method: 'DELETE',
            headers: this._headers,
            body: JSON.stringify(),
            credentials: 'include'
        });
    } 

    //–°—Ç–∞—Ç—É—Å –ª–∞–π–∫–∞
    changeLikeCardStatus(card, isLiked) {
        if (isLiked) {
            return this.deleteLikeCard(card)
        }
        else {
            return this.addLikeCard(card)
        }
    }

    //–ë–ª–æ–∫ —Ä–∞–±–æ—Ç—ã —Å –ø—Ä–æ—Ñ–∏–ª–µ–º
    //–ü–æ–ª—É—á–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    getUserInfoAbout() {
        return this._addCommonFetchForAllApis(`${this._baseUrl}/users/me`, {
            method: "GET",
            headers: this._headers,
            credentials: 'include'
        })
    }

    //–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –¥–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    changeInfo(userData) {
        return this._addCommonFetchForAllApis(`${this._baseUrl}/users/me`, {
            method: 'PATCH',
            headers: this._headers,
            body: JSON.stringify({
                name: userData.name,
                about: userData.about
            }),
            credentials: 'include'
        }); 
    }

    //–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –∞–≤–∞—Ç–∞—Ä –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    changeUserAvatar(imgSrc){
        return this._addCommonFetchForAllApis(`${this._baseUrl}/users/me/avatar`, {
            method: 'PATCH',
            headers: this._headers,
            body: JSON.stringify({
                avatar: imgSrc
            }),
        });
    }
}

export const api = new Api({
    baseUrl: `https://api.ostin.student.nomoredomains.club`,
    headers: {
        'Authorization': `Bearer ${localStorage.getItem('jwt')}`,
        'Content-Type': "application/json",
    },
});
